.container {
  width: 672px;
  margin: 0 auto;
  padding: 26px;
  padding-top: 22px;
  box-sizing: border-box;
}

.cards {
  display: flex;
  flex-direction: row;
  gap: 10px;
  flex-wrap: wrap;
}

.modalContainer {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  background: #004980c0;
}

.header {
  display: flex;
  justify-content: space-between;
  align-items: end;
  margin-bottom: 35px;
}

.timer {
  display: flex;
  align-items: end;

  color: #fff;
  font-family: StratosSkyeng;
  font-size: 64px;
  font-style: normal;
  font-weight: 400;
  line-height: 72px;
}

.previewText {
  font-size: 30px;
  line-height: 30px;
  margin-top: 34px;
  margin-bottom: 10px;
}
.previewDescription {
  font-size: 18px;
  line-height: 18px;
}

.timerValue {
  text-align: right;
}

.timerDescription {
  color: #fff;
  font-variant-numeric: lining-nums proportional-nums;
  font-family: StratosSkyeng;
  font-size: 16px;
  font-style: normal;
  font-weight: 400;
  line-height: 32px;

  margin-bottom: -12px;
}
.mistakes {
  font-size: 20px;
  font-weight: 500;
  color: #ff4d4f; 
  margin-left: 5px; 
  display: flex;
  align-items: center;
}

.mistakes span {
  font-weight: bold;
  margin-left: 5px;
}

.powerBox {
  position: relative;
  cursor: pointer;
}

.tooltipContainer {
  position: relative;
  display: inline-block;
}

.tooltip {
  display: none; 
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  border-radius: 8px;
  padding: 10px;
  z-index: 1000;
}

.tooltipImage {
  width: 222px;
  height: 223px;
}

.tooltipContainer:hover .tooltip {
  display: block; 
}

